import React from "react"
import Container from "react-bootstrap/Container"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Image from "react-bootstrap/Image"
import "./dayCard.css"
import {useSpring, animated} from 'react-spring'

function convertToFar(num)
{
    return Math.floor(num * (9/5) + 32)
}
function DayCard(props){
   

    const [props3, setProps] = useSpring(()=>({
                config: { duration: 500 },
                to: {
                    opacity: 1,
                    transform:'translate3d(0px,0,0) scale(1) rotateX(0deg)'},
                from: {
                    opacity: 0,
                    transform: 'translate3d(200px,0,0) scale(1.5) rotateX(90deg)',
                }
            }))

    let thisDate = new Date(props.data.dt_txt)
    let weekday = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'][thisDate.getDay()]
    return (
        <animated.div style={props3}>
        <Container className="dayComp" 
        onMouseEnter={
            ()=>(setProps({to: {
                            transform:'translate3d(0px,0,0) scale(1.1) rotateX(0deg)'}
                        }))
                            }
        
            onMouseLeave={
            ()=>(setProps({to: {
                            transform:'translate3d(0px,0,0) scale(1) rotateX(0deg)'}}))
                            }
        >


            <Row>
                 <Col><span>{weekday} at {thisDate.getHours()<10?"0":null}{thisDate.getHours()}:{thisDate.getMinutes()<10?"0":null}{thisDate.getMinutes()}</span></Col>
            </Row>
            <Row>
                <Col>
            <Image src={`http://openweathermap.org/img/wn/${props.data.weather[0].icon}@2x.png`}
                     roundedCircle/>
                    </Col>
            </Row>
            <Row>
                <Col><span> {props.data.weather[0].description}</span></Col>
            </Row>
            <Row>
                <Col>
                    <span>
                        {
                            !props.far?
                            (`${Math.floor(props.data.main.temp_max)}째C /${Math.floor(props.data.main.temp_min)}째C`):
                            (`${convertToFar(props.data.main.temp_max)}째F /${convertToFar(props.data.main.temp_min)}째F`)
                     }
                     </span>
                     </Col>
            </Row>
            
        </Container>
        </animated.div>
        
    )
    
}



export default DayCard