import React,{Component} from 'react';
import './App.css';
import DayCard from "./DayCard.js"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"

import Button from "react-bootstrap/Button"
import Container from 'react-bootstrap/Container'
import ButtonGroup from 'react-bootstrap/ButtonGroup'
import {animated} from 'react-spring'


class App extends Component  {

constructor(){
  super()
  this.state = { 
                daysInfo:[],
                allData:[],
                isFareinhit: false,
                cufrrentStart:0, 
                city:"Tel Aviv",
                props3:{
                        config: { duration: 500 },
                        opacity: 1,
                  from: {opacity: 0}
                        }
              }
  this.changeDeg = this.changeDeg.bind(this)
  this.updateData = this.updateData.bind(this)
}

componentDidMount(){
  fetch("https://api.openweathermap.org/data/2.5/forecast?lat=32.0809&lon=34.7806&APPID=e85e2d2fc4b484bda5b322b19d4dbe43&units=metric")
      .then(response =>response.json())
      .then(data =>
        {
          return this.setState({allData:data.list,daysInfo:data.list.slice(0,5),city:data.city.name})
        })

    
}

changeDeg(){
  this.setState((prevState)=>{return {isFareinhit:!prevState.isFareinhit}})

}

updateData(lat,lon){

  fetch(`https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&APPID=e85e2d2fc4b484bda5b322b19d4dbe43&units=metric`)
      .then(response =>response.json())
      .then(data =>
        {
          return this.setState({allData:data.list,daysInfo:data.list.slice(0,5),city:data.city.name, cufrrentStart:0, cityNum:lat+lon})
        })

}
  render(){

    
    const dayComponents =
     this.state.daysInfo.map(data =>(<div key={this.state.cityNum+data.dt} ><DayCard key={data.dt} data={data} far={this.state.isFareinhit} /></div>))
    return (
      <animated.div style={this.state.props3} className="App">
        <header className="App-header">
          <Container>
            <Row>
              <Col>
               <span className="deepshadow title">This is a 15 hours forcast Weather App </span>
              </Col>
            </Row>
            
            <Row>
              <Col>
                <span className="deepshadow title">Showing weather in {this.state.city}</span>
              </Col>
            </Row>

            <Row><p></p></Row>

            <Row>
              <Col>
                <ButtonGroup>
                  
                    <Button variant="primary" onClick={()=>this.updateData(32.0809,34.7806)} disabled={this.state.city==="Tel Aviv"}>Tel Aviv</Button>
                    <Button variant="primary" onClick={()=>this.updateData(32.8191,34.9984)} disabled={this.state.city==="Haifa"}>Haifa</Button>
                    <Button variant="primary"onClick={()=>this.updateData(31.2525,34.7906)} disabled={this.state.city==="Beersheba"}>Beersheba</Button>
                  
                </ButtonGroup>
              </Col>
            </Row>

            <Row><p></p></Row>

            <Row>
              <Col>
                <Button onClick={(event)=>this.changeDeg()}>
                  Convert to {this.state.isFareinhit ?"째C":"째F"}
                </Button>
              </Col>
            </Row>
            
            <Row>
              <Col>
                <Container >
                  <Row> 
                  
                    <Col>
                          {dayComponents[0]}    
                    </Col>
                    
                    <Col>
                      
                          {dayComponents[1]}

                    </Col>
                    <Col>
                      
                        
                          {dayComponents[2]}
                     
                    </Col>
                    <Col>
                      
                          {dayComponents[3]}
                      
                    </Col>
                    <Col>
                      
                          {dayComponents[4]}
                      
                    </Col>
                  </Row>
                </Container>
                
              </Col>
            </Row>
            <Row><p></p></Row>
          </Container>
          
        </header>
      </animated.div>
    );
  }
}

export default App;
